.consultation-one {
    &__wrap {
        padding: 0;
    }
    &__row {
        --bs-gutter-x: 0;
        flex-wrap: wrap;
    }
    &__colimg {
        width: calc(calc(44.2% - 0px) / 1);
        &__image {
            position: relative;
            &__mainimg {
                width: 100%;
                & > img {
                    width: 100% !important;
                }
            }
            &__circlemediam {
                position: absolute;
                right: 37px;
                bottom: 0;
                z-index: 1;
                width: 416px;
                height: 416px;
                border: 4px solid #fff;
                border-radius: 50%;
                overflow: hidden;
                & > img {
                    // width: 100% !important;
                    object-fit: cover;
                    object-position: center center;
                }
            }
            &__circlesmall {
                position: absolute;
                right: 347px;
                bottom: 90px;
                z-index: 2;
                width: 352px;
                height: 352px;
                border: 4px solid #fff;
                border-radius: 50%;
                overflow: hidden;
                & > img {
                    width: 100% !important;
                }
            }
        }
    }

    &__contentwrap {
        width: calc(calc(55.8% - 0px) / 1);
        &__content {
            .sec-title {
                &__title {
                    text-align: left;
                }
                &__text {}
            }
        }
    }
}